input:not([type="checkbox"]), textarea {
    width: 100%;
    height: 2.5em;
    background-color: transparent;
    font-size: 1em;
    color: $colorText;
    outline: none;
    font-family: $fontText;
    -webkit-appearance: none;
    border-radius: 0;

    @include placeholder {
        color: $colorMuted; } }

label {
    user-select: none; }

.form-column, .form-group-column {
    float: left;
    vertical-align: top;
    display: inline-block; }

.form-column {
    &--full {
        width: 100%; }
    &--primary {
        width: 65%;
        @media screen and (max-width: $thresholdCollapse) {
            width: 100%; } }
    &--secondary {
        width: 35%;
        border-left: 1px solid $colorLightBG4;
        background-color: $colorLightBG2;
        @media screen and (max-width: $thresholdCollapse) {
            width: 100%;
            border-left: 0;
            border-top: 1px solid $colorLightBG4; } } }

.form-group-column {
    &--field {
        width: 65%;
        @media screen and (max-width: $thresholdMicro) {
            width: 100%; } }

    &--help {
        width: 35%;
        font-size: 0.75em;
        color: $colorMutedLess;
        padding: 2.75em 0 0 1.33333333em;
        @media screen and (max-width: $thresholdMicro) {
            display: none; } } }

.form-buttons {
    @include position(absolute, null 2em -1.25em null);
    box-shadow: $shadowDownLight;
    background-color: $colorLightBG1;

    &--displaced {
        bottom: 1.75em; }

    @media screen and (max-width: $thresholdMicro) {
        right: 1em; } }

.form-group {
    @include clearfix;
    margin: 0.5em 0 1.5em;

    &--inline {
        display: inline-block;
        min-width: 24%;
        @media screen and (max-width: $thresholdCollapse) {
            min-width: 31%; }
        @media screen and (max-width: $thresholdMicro) {
            min-width: 48%; } }

    &__errors {
        text-align: right;
        font-size: 0.75em;
        color: $colorDanger;
        padding: 0.25em 0; }

    &__label {
        text-align: left;
        font-size: 0.875em;
        font-weight: 700;
        font-family: $fontHeading;
        color: $colorMuted;
        @include transition(color $durationShort);
        padding-left: 1em;
        position: relative;
        display: block;
        line-height: 1.25em;

        &:before {
            content: '';
            width: 0.75em;
            height: 1px;
            position: absolute;
            bottom: 0.25em;
            border-bottom: 1px solid $colorOutline;
            left: 0;
            @include transition(border-bottom-color $durationShort); }

        &--focus {
            color: $colorEmphasis;
            &:before {
                border-bottom-color: $colorEmphasis; } }

        &--error {
            color: $colorDanger;
            &:before {
                border-bottom-color: $colorDanger; } } }

    input, textarea {
        border-bottom: 1px solid $colorLightBG4;
        @include transition(border-bottom $durationShort);

        &:focus {
            border-bottom: 1px solid $colorEmphasis; } }

    textarea {
        max-width: 28em;
        min-height: 6em;
        resize: vertical;
        margin-top: 0.5em; }

    &__select {
        position: relative;
        border-bottom: 1px solid $colorLightBG4;

        i {
            @include position(absolute, 0.625em 0.375em null null);
            color: $colorOutline;
            font-size: 0.875em;
            @include transition(color $durationShort); }

        select {
            height: 2em;
            position: relative;
            width: 100%;
            z-index: 1;
            cursor: pointer;
            background: transparent;
            -webkit-appearance: none;
            border: 0;
            box-shadow: none;
            outline: none;
            padding-right: 1.5em;
            font-size: 1em;
            color: $colorText; } }

    &__checkbox {
        cursor: pointer;
        & > span {
            display: inline-block;
            margin-top: 0.375em;
            color: $colorDanger;
            @include transition(color $durationShort);
            .icon-confirm {
                display: none; } }

        input {
            display: none;

            &:checked + span {
                color: $colorEmphasis;
                .icon-confirm {
                    display: inline-block; }
                .icon-cancel {
                    display: none; } } } }

    &__checkbox-icon {
        span {
            font-family: $fontHeading;
            font-size: 0.875em; }
        &:before {
            font-size: 0.75em; } }

    &__password {
        background-color: $colorLightBG3;
        height: 0.25em;
        opacity: 0.7;

        .password-strength {
            height: 0.25em;
            width: 0%;
            @include transition(all 150ms);

            &--veryweak {
                background-color: $colorDanger; }
            &--weak {
                background-color: #e67e22; }
            &--medium {
                background-color: $colorEmphasis; }
            &--strong {
                background-color: #2ecc71; } } }

    &__color {
        .minicolors-swatch {
            top: 0.625em !important; }
        input {
            height: 2.5em !important;
            min-width: 6em;
            padding-left: 2em !important; } }

    &--error {
        input, textarea {
            border-bottom-color: $colorDanger; } }

    &--inverted {
        input, textarea, .form-group__select {
            border-bottom-color: $colorOutline; }

        input:not([type="checkbox"]), textarea, select {
            color: $colorLightText; } } }

.related-items {
    background-color: $colorLightBG1;
    box-shadow: $shadowDownLight;
    margin-top: 0.5em;

    &.ui-sortable {
        li {
            cursor: move; } }

    .placeholder {
        content: ' ';
        height: 2.5em;
        background-color: $colorLightBG2;
        font-size: 0.875em; } }

.related-item {
    border-bottom: 1px solid $colorLightBG4;
    line-height: 2.5em;
    font-size: 0.875em;
    padding: 0 3em 0 1em;
    position: relative;
    width: 100%;
    @include ellipsis;

    &__close {
        @include position(absolute, 0 0 null null);
        @include size(2.5em);
        cursor: pointer;
        text-align: center;
        line-height: 2.5em !important;
        color: $colorMuted; }

    &:last-child {
        border-bottom: 0; } }

.related-search {
    position: relative;
    height: 2.5em;

    &__results {
        @include position(absolute, 2.5em null null 0);
        width: 100%;
        background-color: $colorLightBG1;
        box-shadow: $shadowDownLight;
        z-index: 100;

        &--hidden {
            display: none; } }

    &__result {
        border-bottom: 1px solid $colorLightBG4;
        line-height: 2.5em;
        cursor: pointer;
        font-size: 0.875em;
        padding: 0 1em;
        position: relative;
        width: 100%;
        @include ellipsis;

        &:last-child {
            border-bottom: 0; }

        &--selected {
            background-color: $colorEmphasis;
            color: $colorLightText; } }

    &__input {
        @include position(absolute, 0 null null 0);
        @include size(100%);
        z-index: -1;
        opacity: 0; } }
